<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
            class="w-100"
        >
            <!-- eslint-disable  -->

            <template v-slot:item.direction="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.direction }}</span>
                </div>
            </template>
            <template v-slot:item.type="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.type }}</span>
                </div>
            </template>
            <template v-slot:item.title="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.title }}</span>
                </div>
            </template>
            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>
            <template v-slot:item.mass="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.mass }} kg</span>
                </div>
            </template>
            <template v-slot:item.kg_price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.kg_price) }} so`m</span>
                </div>
            </template>
            <template v-slot:item.sum="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.sum) }} so`m</span>
                </div>
            </template>
            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <!-- <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'warehouse_yoqa_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'warehouse_yoqa_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>
        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>




        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                     
                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
    </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],  
    components:{
        DeleteModal,
        NoData
    },
    data(){
        return{
            page: 1,
            pageCount: 0,
            loading: false,
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Partiya', value: 'direction', align: 'start', class: 'table-header'},
                {text: 'Turi', value: 'type', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Massa (kg)', value: 'mass', align: 'center', class: 'table-header'},
                {text: 'KG narxi', value: 'kg_price', align: 'center', class: 'table-header'},
                {text: 'Summasi', value: 'sum', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],

            items: [],
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_collar_income': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_COLLAR_INCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                material_income: state => state.warehouse.material_income,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        )
    },
    methods:{
        ...mapActions(
            {
                fetchIncome: 'warehouse/fetchIncome',
                fetchMaterialIncome: 'warehouse/fetchMaterialIncome'
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchMaterialIncome({page: this.page, status: 3, type: 'collar', search: this.searchValue, date: this.dateFilterValue})
            this.items = this.material_income.data

            this.page = this.material_income.current_page;
            this.pageCount = Math.ceil(this.material_income.total / 10);

            this.items.forEach((element) => {
                element.status = true;
                element.direction = element.order.factory.name
                element.type = element.order.product.product_category.name
                element.title = element.order.product.name
                element.code = element.order.product.code
                element.mass = element.amount
                element.kg_price = element.order.price
                element.sum = element.amount * element.order.price
                if(element.created_at)
                element.date = this.$dayjs(element.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        }
    }
}
</script>

