<template>
    <div>
        <v-row>
            <v-col>
                <v-data-table
                    :headers="headers"
                    :items="items"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                    class="w-100"
                >
                    <!-- eslint-disable  -->

                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.title="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.title }}</span>
                        </div>
                    </template>
                    <template v-slot:item.whom="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.whom }}</span>
                        </div>
                    </template>
                    <template v-slot:item.mass="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.mass }} kg</span>
                        </div>
                    </template>

                    <!-- <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center justify-center">
                            <div class="cursor-pointer mr-2 mt-1" 
                            v-if="$can('update', 'warehouse_yoqa_jarayondagi')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" 
                            v-if="$can('delete', 'warehouse_yoqa_jarayondagi')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template> -->
            
                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>
                    
                </v-data-table>
            </v-col>
            <div style="border-right: 1px solid #ececec"></div>
            <v-col>
                <v-data-table
                    :headers="headers"
                    :items="items2"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                    class="w-100"
                >
                    <!-- eslint-disable  -->

                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.title="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.title }}</span>
                        </div>
                    </template>
                    <template v-slot:item.whom="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.whom }}</span>
                        </div>
                    </template>
                    <template v-slot:item.mass="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.mass }} kg</span>
                        </div>
                    </template>

                    <!-- <template v-slot:item.actions="{item}">
                        <div class="d-flex align-center">
                            <div class="cursor-pointer mr-2 mt-1" 
                            v-if="$can('update', 'warehouse_yoqa_jarayondagi')">
                                <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                            </div>
                            <div class="cursor-pointer mt-1" 
                            v-if="$can('delete', 'warehouse_yoqa_jarayondagi')">
                                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                            </div>
                        </div>
                    </template> -->

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>
                    
                </v-data-table>
            </v-col>
        </v-row>

        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>
        
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],  
    components: {
        DeleteModal,
        NoData
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Kod', value: 'code', align: 'start', class: 'table-header'},
                {text: 'Turi', value: 'type', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Kimga', value: 'whom', align: 'center', class: 'table-header'},
                {text: 'Massa (kg)', value: 'mass', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
           
            items: [],
            items2: [],
           
            // ============= SECONDARY_DATA =============
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_collar_inprocess': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_COLLAR_INPROCESS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                jarayondagi_products: state => state.warehouse.jarayondagi_products,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                fetchJarayondagiProducts: 'warehouse/fetchJarayondagiProducts',
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchJarayondagiProducts({page: this.page, status: 3, search: this.searchValue, date: this.dateFilterValue})

            this.page = this.jarayondagi_products.current_page;
            this.pageCount = Math.ceil(this.jarayondagi_products.total / 20);
            this.items2 = this.jarayondagi_products.data.slice(10, this.jarayondagi_products.data.length);
            this.items = this.jarayondagi_products.data;


            this.items.forEach((element) => {
                element.type = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })

            this.items2.forEach((element) => {
                element.type = element.product.product_category.name
                element.title = element.product.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })
            this.loading = false
        },



    }
}
</script>

